import CustomButton from "components/CustomButton";
import CustomForm from "components/CustomForm";
import useNotification from "hooks/Notifications";
import React from "react";
import { useDispatch } from "react-redux";
import { contactUs } from "redux/Actions/Auth";
import { INPUT_TYPES, SNACK_VARIANT, STRINGS } from "shared/Constants";
import { Images } from "shared/Images";
import { CONTACT_US_DATA } from "./constant";
import './styles.scss';
function ContactForm(props) {
    const dispatch = useDispatch()
    const [msg, sendNotifications] = useNotification()
    const onSubmit = (data, phone_number) => {
        dispatch(contactUs({ ...data, phone_number }, (response) => {
            sendNotifications({
                msg: response.message,
                variant: SNACK_VARIANT.SUCCESS
            })
        }, (error) => {
            sendNotifications({
                msg: error || STRINGS.SOMETHING_WENT_WRONG,
                variant: SNACK_VARIANT.ERROR
            })
        }))
    };
    return (
        <section className="getintouch_sec">
            <div className="container-fluid">
                <div className="row">
                    <div className="col-lg-6 px-md-0 position-relative">
                        <div className="gmap">
                            <img src={Images.MapPic} alt="" width="100" />
                        </div>
                    </div>
                    <div className="col-lg-6">
                        <div className="p-4 p-lg-5">
                            <div className="title_h2 mb-4">
                                <h2 className="h2 mb-2">Get in Touch</h2>
                                <p>We are here for you! how can we help?</p>
                            </div>
                            <div className="row form_white touch_form">
                                <CustomForm
                                    id="contact-us"
                                    formData={CONTACT_US_DATA}
                                    reCaptcha={false}
                                    renderSubmitButton={false}
                                    onSubmit={onSubmit}
                                />
                                <div className="col-md-12 text-end">
                                    <CustomButton
                                        className="btn btn-md btn-primary"
                                        type={INPUT_TYPES.SUBMIT}
                                        form="contact-us"
                                        title={STRINGS.SUBMIT}
                                    />
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        </section>
    );
}

export default ContactForm;
