import CustomButton from "components/CustomButton";
import CustomForm from "components/CustomForm";
import useNotification from "hooks/Notifications";
import React, { useState } from "react";
import { useDispatch } from "react-redux";
import {
    deleteEducationDetails,
    deleteJobDetails,
    saveEducationData,
    saveJobHistoryData,
} from "redux/Actions/Auth";
import { ERROR_MESSAGES, INPUT_TYPES, LABEL, SNACK_VARIANT, STRINGS } from "shared/Constants";
import { Images } from "shared/Images";
import {
    addEducationDetails,
    addJob,
    checkAndDelete,
    checkAndUpdate,
    createDefaultData,
    createEducationDefaultData,
    createEducationHistory,
    createEducationHistoryList,
    createEducationList,
    createEducationListClick,
    createEducationPayload,
    createHistoryDefault,
    createItemData,
    createJobHistoryData,
    createJobHistoryList,
    createPayloadData,
} from "../../constants";
import CustomEducationForm from "./widget/CustomEducationHistory";

function EducationDetails({ educationData = [], setCvData = () => { } }) {
    const dispatch = useDispatch();
    const [msg, sendNotifications] = useNotification();
    const [educationdetailsData, setEducationdetailsData] = useState([
        {
            type: INPUT_TYPES.TEXT,
            registerName: "institution-name0",
            labelName: LABEL.INSTITUTION_NAME,
            placeholder: STRINGS.ENTER_HERE,
            required: true,
            className: "",
            errorMessages: {
                required: ERROR_MESSAGES.INSTITUTION_NAME,
                pattern: ERROR_MESSAGES.VALID_INSTITUTION_NAME,
            },
        },
        {
            type: INPUT_TYPES.TEXT,
            registerName: "qualification0",
            labelName: LABEL.QUALIFICATIONS,
            required: true,
            className: "",
            errorMessages: {
                required: ERROR_MESSAGES.QUALIFICATIONS,
                pattern: ERROR_MESSAGES.VALID_QUALIFICATIONS,
            },
        },
        {
            type: INPUT_TYPES.DATE,
            registerName: "start-date0",
            labelName: LABEL.START_DATE,
            required: true,
            className: "",
            errorMessages: {
                required: ERROR_MESSAGES.DATE,
                pattern: ERROR_MESSAGES.VALID_DATE,
            },
        },
        {
            type: INPUT_TYPES.DATE,
            registerName: "end-date0",
            labelName: LABEL.END_DATE,
            required: true,
            className: "",
            errorMessages: {
                required: ERROR_MESSAGES.DATE,
                pattern: ERROR_MESSAGES.VALID_DATE,
            },
        },
        {
            type: INPUT_TYPES.TEXT,
            registerName: "description0",
            labelName: LABEL.DESCRIPITON,
            required: true,
            className: "",
            errorMessages: {
                required: ERROR_MESSAGES.DESCRIPITON,
                pattern: ERROR_MESSAGES.VALID_DESCRIPTION,
            },
        },
    ]);
    const [educationHistoryList, setEducationHistoryList] = useState([
        ...createEducationList(educationData.length, educationData),
    ]);
    const [educationAddClick, setEducationAddClick] = useState(false);
    const [historyData, setHistoryData] = useState(createEducationDefaultData(educationData));
    const onSubmit = (data, event) => {
        const payload = createEducationPayload(data);

        dispatch(
            saveEducationData(
                { edu_history: JSON.stringify(payload) },
                (response) => {
                    setHistoryData(createEducationDefaultData(response.data));
                    setCvData((prev) => {
                        return {
                            ...prev,
                            edu_history: [...response.data],
                        };
                    });
                    if (educationAddClick) {
                        setEducationHistoryList([
                            ...createEducationListClick(educationHistoryList, true),
                            addEducationDetails(educationHistoryList.length),
                        ]);
                        setEducationAddClick(false);
                    } else {
                        setEducationHistoryList(
                            createEducationListClick(educationHistoryList, true),
                        );
                    }
                    sendNotifications({
                        msg: response.message,
                        variant: SNACK_VARIANT.SUCCESS,
                    });
                },
                (error) => {
                    sendNotifications({
                        msg: error || STRINGS.SOMETHING_WENT_WRONG,
                        variant: SNACK_VARIANT.ERROR,
                    });
                },
            ),
        );
    };
    const onEditClick = () => {
        setEducationHistoryList(createEducationListClick(educationHistoryList, false));
    };
    const onDeleteClick = (deleteEvent) => {
        const educationHistoryIndex = deleteEvent.target.id;
        if (historyData[educationHistoryIndex]) {
            dispatch(
                deleteEducationDetails(
                    { id: educationData[educationHistoryIndex].id },
                    (response) => {
                        // now we are going to update the values
                        educationData.splice(educationHistoryIndex, 1);
                        setCvData((prev) => {
                            return {
                                ...prev,
                                edu_history: [...educationData],
                            };
                        });
                        educationHistoryList.splice(educationHistoryIndex, 1);
                        historyData.splice(educationHistoryIndex, 1);
                        setHistoryData([...historyData]);
                        setEducationHistoryList(educationHistoryList);
                        sendNotifications({
                            msg: response.message,
                            variant: SNACK_VARIANT.SUCCESS,
                        });
                    },
                    (error) => {
                        sendNotifications({
                            msg: error || STRINGS.SOMETHING_WENT_WRONG,
                            variant: SNACK_VARIANT.ERROR,
                        });
                    },
                ),
            );
        } else {
            // we need to splice the list array
            educationHistoryList.splice(educationHistoryIndex, 1);
            historyData.splice(educationHistoryIndex, 1);
            setHistoryData([...historyData]);
            setEducationHistoryList([...educationHistoryList]);
        }
    };

    console.log(educationHistoryList, historyData, "both wants to know");
    return (
        <div className="card mb-4">
            <div className="card-body">
                <div className="title_icon d-flex mb-3">
                    <h6 className="h6 mb-0">{STRINGS.EDUCATIONDETAIL}</h6>
                    <em className="ms-2" onClick={onEditClick}>
                        <img src={Images.Pencil} width="16" />
                    </em>
                </div>
                <CustomEducationForm
                    id="educationHistory"
                    formData={educationHistoryList}
                    onSubmit={onSubmit}
                    onDeleteClick={onDeleteClick}
                    defaultValues={createHistoryDefault(historyData)}
                />
                <div className="btn_group text-center">
                    <button
                        type={"submit"}
                        form={"educationHistory"}
                        // id={"addMoreClick"}
                        onClick={() => {
                            setEducationAddClick(true);
                        }}
                        className="btn btn-md btn-outline-secondary"
                    >
                        <svg className="me-2" width="18" height="19" viewBox="0 0 29 28" fill="none" xmlns="http://www.w3.org/2000/svg">
                            <path d="M14.9512 0C13.9337 0 13.1091 0.824671 13.1091 1.84211V12.1579H2.79328C1.77584 12.1579 0.951172 12.9826 0.951172 14C0.951172 15.0174 1.77584 15.8421 2.79328 15.8421H13.1091V26.1579C13.1091 27.1753 13.9337 28 14.9512 28C15.9686 28 16.7933 27.1753 16.7933 26.1579V15.8421H27.1091C28.1265 15.8421 28.9512 15.0174 28.9512 14C28.9512 12.9826 28.1265 12.1579 27.1091 12.1579H16.7933V1.84211C16.7933 0.824671 15.9686 0 14.9512 0Z" fill="#104651" />
                        </svg>
                        {STRINGS.ADD_JOB}
                    </button>
                    <button
                        type={"submit"}
                        form={"educationHistory"}
                        className="btn btn-md btn-primary"
                    >
                        {STRINGS.SAVE}
                    </button>
                </div>
            </div>
        </div>
    );
}

export default EducationDetails;
