import { LOGIN, SET_ABOUT_US, SET_AUTH_TOKEN, SET_FOOTER, SET_HOME_PAGE, SET_TOKEN } from "../Actions/Auth";
import { ACTION_STATES } from "../Actions/ActionStates";

const initialState = {
    homepage: {},
    aboutUs: {},
    footer:{}
    
};
const dynamicContentReducer = (state = initialState, action) => {
    switch (action.type) {
        case SET_HOME_PAGE: {
            return {
                ...state,
                homepage: action.homepage,
            };
        }
        case SET_ABOUT_US: {
            return {
                ...state,
                aboutUs: action.aboutUs,
            };
        }
        case SET_FOOTER: {
            return {
                ...state,
                footer: action.footer,
            };
        }
        default: {
            return state;
        }
    }
};

export default dynamicContentReducer;
